<template>
  <v-app>
    <v-navigation-drawer v-model="sideNav" fixed temporary>
      <v-divider/>
      <v-list dense>
        <v-list-tile v-for="item in menuItems" :key="item.name" :to="item.path">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>{{ item.name }}</v-list-tile-content>
        </v-list-tile>
        <v-divider/>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar dark class="primary">
      <v-toolbar-side-icon @click.stop="sideNav = !sideNav"/>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer">Vots per Municipi</router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-list-tile v-for="item in menuItems" :key="item.name" :to="item.path">
          <v-btn flat>{{ item.name }}</v-btn>
        </v-list-tile>
      </v-toolbar-items>
    </v-toolbar>
    <v-content>
      <v-data-table :headers="headers" :items="vots" class="elevation-1">
      <template v-slot:items="vots">
        <td>{{ vots.item.municipi }}</td>
        <td class="text-xs-right">{{ vots.item.votsPDECAT }}</td>
        <td class="text-xs-right">{{ vots.item.votsERC }}</td>
        <td class="text-xs-right">{{ vots.item.votsPodemos }}</td>
        <td class="text-xs-right">{{ vots.item.votsComuns }}</td>
      </template>
      </v-data-table>
    </v-content>
  </v-app>

</template>

<script>
import json from '../data/vots_per_municipi.json'
export default {
  data () {
    return {
      vots: json,
      sideNav: false,
      headers: [
        { text: 'Municipi', align: 'left', sortable: true, value: 'Municipi' },
        { text: 'votsPDECAT', align: 'right', sortable: true, value: 'votsPDECAT' },
        { text: 'votsERC', align: 'right', sortable: true, value: 'votsERC' },
        { text: 'votsPodemos', align: 'right', sortable: true, value: 'votsPodemos' },
        { text: 'votsComuns', align: 'right', sortable: true, value: 'votsComuns' }
      ]
    }
  },
  computed: {
    menuItems () {
      return this.$router.options.routes.filter(r => r.beforeEnter == null)
    }
  }
}
</script>
